function Cart(props) {
    const cart = props.cart;
    const goodsObj = props.goodsObj;
    let total = 0;

    for (let key in cart) {
        total += goodsObj[key]['cost'] * cart[key];
    }

    let totalRender = '';
    if (total > 0) {
        totalRender = `all amount ` + total;
    } else {
        totalRender = `no pre-selected items`;
    }

    return (
        <div>
            <p className='title-name'>cart</p>
            <table>

                <thead>
                    <tr className='table-thead'>
                        <th>#</th>
                        <th>product</th>
                        <th>cost</th>
                        <th>quantity</th>
                        <th>total</th>
                        <th colspan="4"></th>
                    </tr>
                </thead>
                <tbody>
                    {Object.keys(cart).map((item, index) => <tr key={item + goodsObj[item]['title']} >
                        <td>{index + 1}</td>
                        <td>{goodsObj[item]['title']}</td>
                        <td>{goodsObj[item]['cost']}</td>
                        <td>{cart[item]}</td>
                        <td>{goodsObj[item]['cost'] * cart[item]}</td>
                        <td><img src={goodsObj[item]['image']} alt={goodsObj[item]['title']} className='img' /></td>
                        <td><button className='add-to-cart min-add-to-cart' data-key={goodsObj[item]['articul']}>add</button></td>
                        <td><button className='del-to-cart min-del-to-cart' data-key={goodsObj[item]['articul']}>del</button></td>
                    </tr>)}
                </tbody>
            </table>
            <div className='table-block'>
                <p className="total-coast">{totalRender}</p>
            </div>
        </div>
    );


}

export default Cart;