import React from 'react';
import { useSelector } from 'react-redux';
import {
    selectGoods
} from '../../store/goodsSlice';
import {
    selectCart
} from '../../store/cartSlice';
import Cart from '../components/Cart';
import { useDispatch } from 'react-redux';
import { increment, minus } from '../../store/cartSlice';


function CartList() {
    const goods = useSelector(selectGoods);
    const cart = useSelector(selectCart);
    const dispatch = useDispatch();
    // переиндексирую массив товара
    const goodsObj = goods.reduce((accum, item) => {
        accum[item['articul']] = item;
        return accum;
    }, {});

    let clickHandler = (event) => {
        event.preventDefault();
        let t = event.target;
        if (!(t.classList.contains('add-to-cart') || t.classList.contains('del-to-cart'))) return true;

        if (t.classList.contains('add-to-cart')) {
            dispatch(increment(t.getAttribute('data-key')));
        } else {
            dispatch(minus(t.getAttribute('data-key')));
        }
    }



    return (
        <div className='cart-field' onClick={clickHandler}>
            <Cart cart={cart} goodsObj={goodsObj} />
        </div>
    );
}

export default CartList;