import GoodsList from '../Cart/containers/GoodsList';
import CartList from '../Cart/containers/CartList';

function Cart() {
  return (
    <div className='container'>
      <div className='cart-component'>
        <div className='good-list'>
          <GoodsList />
        </div >
        <div className='cart-list'>
          <CartList />
        </div>
      </div>
    </div>
  );
}

export default Cart;