import './App.css';
import Home from './components/Home';
import RandomGenerate from './components/RandomGenerate';
import Cart from './components/Cart';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';
import React, { useEffect } from 'react';

function App() {




  const isMobile = {
    Android: function () {
      return navigator.userAgent.match(/Andriod/i);
    },
    BlackBerry: function () {
      return navigator.userAgent.match(/BlackBerry/i);
    },
    iOS: function () {
      return navigator.userAgent.match(/iPhone|iPad|iPod/i);
    },
    Opera: function () {
      return navigator.userAgent.match(/Opera Mini/i);
    },
    Windows: function () {
      return navigator.userAgent.match(/IEMobile/i);
    },
    Safary: function () {
      return navigator.userAgent.match(/Mobile/i);
    },
    any: function () {
      return (
        isMobile.Android() ||
        isMobile.BlackBerry() ||
        isMobile.iOS() ||
        isMobile.Opera() ||
        isMobile.Safary() ||
        isMobile.Windows());
    }
  };



  const wrapper = React.createRef();
  const menuArrow = React.createRef();
  const menu__icon = React.createRef();
  const menu__body = React.createRef();
  const buttonArrow = React.createRef();
  const lock = React.createRef();


  useEffect(() => {
    if (isMobile.any()) {
      wrapper.current.classList.add('_touch');
      menuArrow.current.classList.toggle('_active');
      buttonArrow.current.classList.toggle('_active');
    } else {
      document.body.classList.add('_pc');
    }

    if (menu__icon.current) {

    }
  });

  function iconMenu() {
    lock.current.classList.toggle('_lock');
    menu__icon.current.classList.toggle('_active');
    menu__body.current.classList.toggle('_active');
  }

  function unLock() {
    if (wrapper.current.classList.contains('_touch') &&
      menu__icon.current.classList.contains('_active')) {
      lock.current.classList.remove('_lock');
      menu__icon.current.classList.remove('_active');
      menu__body.current.classList.remove('_active');
    }
  }

  function menuHide() {
    menuArrow.current.classList.toggle('_active');
  }


  return (
    <>
      <Router>
        <div ref={lock}>
          <div className="wrapper" ref={wrapper}>
            <header className="header">
              <div className="header__container">
                <div className="header__logo_block">
                  <Link to='/' className="header__logo" onClick={unLock}>Разработка на React</Link>
                </div>
                <div className="header__menu menu">
                  <div className="menu__icon" ref={menu__icon} onClick={iconMenu}>
                    <span></span>
                  </div>
                  <nav className="menu__body" ref={menu__body}>
                    <ul className="menu__list" >
                      <li ref={menuArrow} className='_active'><Link to='/rendom-generate' className="menu__link" onClick={unLock}>Написанные компоненты React</Link>
                        <span className="menu__arrow " ref={buttonArrow} onClick={menuHide}></span>
                        <ul className="menu__sub-list">
                          <li><Link to='/rendom-generate' className="menu__link" onClick={unLock}>Генератор случайных чисел</Link></li>
                          <li><Link to='/cart' className="menu__link" onClick={unLock}>Корзина</Link></li>
                        </ul>
                      </li>
                      <li><Link to='/' className="menu__link" onClick={unLock}>Информация</Link></li>
                    </ul>
                  </nav>
                </div>
              </div>
            </header>
          </div>
        </div>

        <Switch>
          <Route path='/rendom-generate' component={RandomGenerate}></Route>
          <Route path='/cart' component={Cart}></Route>
          <Route exact path='/' component={Home}></Route>
        </Switch>
      </Router>
      <footer></footer>
    </>
  );
}

export default App;
